body {
  margin: 0;
  font-family: 'Source Sans Pro', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.stretchy{
  display:flex;
  align-self:stretch;
}

@media (max-width: 768px){
  .taskWrap{
    width:100%;
  }
}
